import {QRCode} from 'react-qrcode-logo'
import './App.css'
import {useState} from 'react'


export default function Qr(){
  const[text,setText]=useState("Hello")
  const[value,setValue]=useState("Hello")
  const handleOnChange = (event)=> {
setText(event.target.value);

}
  const btn=()=>{
    setValue(text);
  }
  return(
    <>
        <div className='container dow'>
<div class="form-floating">
  <textarea class="form-control of" style={{borderRadius:'20px'}} placeholder="Leave a comment here" value={text} onChange={handleOnChange} id="floatingTextarea"></textarea>
  <label for="floatingTextarea">Enter text here</label>
</div>
<div className='center'>
    <button className="btn" style={{color:'black',backgroundColor:'#00B4D8',marginTop:'20px',width:'10rem'}} onClick={btn}> Convert</button>
    </div>
    </div>
    <div className="center damn">
    <QRCode className='' value={value}/>
    </div>


    </>
    );
}